@keyframes minutes-hand {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes seconds-hand {
  100% {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50vmin;
  justify-content: center;
  align-self: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes-hand {
  z-index: 0;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite minutes-hand;
  position: absolute;
  top: 20vmin;
}

.stopwatch__seconds-hand {
  z-index: 0;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite seconds-hand;
  position: absolute;
  top: 2vmin;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50vmin;
  align-self: center;
  width: 5vmin;
  height: 5vmin;
}

.speed-up .stopwatch__seconds-hand {
  animation-name: seconds-hand;
  animation-duration: 60s;
}

.speed-up .stopwatch__minutes-hand {
  animation-name: seconds-hand;
  animation-duration: 3600s;
}
